.card {
    background: #fff;
    padding: 6px;
    border-radius: 2px;
    height: 180px;
    margin: 1rem;
    position: relative;
    width: 180px;
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    cursor: pointer;
    float: left;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}

.card:hover{
  -ms-transform: scale(1.2); /* IE 9 */
    -webkit-transform: scale(1.2); /* Safari 3-8 */
    transform: scale(1.2); 
}
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background-image: url(https://orig00.deviantart.net/5b2c/f/2015/107/c/d/orange_yellow_rings_blue_center_4_by_squadhater2002-d8q11ih.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
  .card > .img-container > img {
    width: 50%;
    margin-top: 25px;
    
  }
  
  
 
  
  
 
  

  
 
  


